  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1rem 1rem;
  border-bottom: 1px solid #cdcdcd;
  border-top-left-radius: subtract(2rem, 1px);
  border-top-right-radius: subtract(2rem, 1px);
}
