}

.modal-footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  padding: 1rem 32px-0.25rem;
  border-top: 1px solid #cdcdcd;
  border-bottom-right-radius: subtract(2rem, 1px);
  border-bottom-left-radius: subtract(2rem, 1px);
}
.modal-footer > * {
  margin: 0.25rem;
}

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

